<!-- 导航子组件 -->
<template>
    <el-submenu v-if="item.children && item.children.length > 0"
                :index='`${item.name}`'>
        <template slot="title">
            <p>{{item.name}}</p>
        </template>
        <item-side v-for="child in item.children"
                    :item="child"
                    :key="child.path"/>
    </el-submenu>
    <el-menu-item v-else
                :index='`${item.path}`'>
        {{item.name}}
    </el-menu-item>
</template>

<script>
export default {
    name: 'itemSide',
    props: ['item']
}
</script>

