<!-- 头部组件 -->
<template>
    <div class='console_header'>
        <div class="userPic"
            @mouseenter="showUserNav"
            @mouseleave="leaveUserNav">
            <ul v-if="isShowUserNav" class="userNav">
                <li>{{userInfo.loginName}}</li>
                <li @click='updatePwd'>修改密码</li>
                <li @click="logout">退出登录</li>
            </ul>
        </div>
    </div>
</template>

<script>
import Cookies from 'js-cookie';
import { mapActions } from 'vuex';
export default {
    data() {
        return {
            isShowUserNav: false,
            userInfo: JSON.parse(Cookies.get('userInfo'))
        }
    },
    methods: {
        ...mapActions({
            clearUserInfo: 'clearUserInfo'
        }),
        showUserNav () {
            this.isShowUserNav = true;
        },
        leaveUserNav () {
            this.isShowUserNav = false;
        },
        // 退出登录
        logout() {
            this.clearUserInfo().then(() => {
                this.$router.push('/login');
            })
        },
        // 更新密码
        updatePwd() {
            this.$router.push('/update');
        }
    }
};
</script>
