    padding-right: 50px;
    display: flex;
    justify-content: flex-end;
    height: 40px;
    border-bottom: 1px solid #999;
}
.userPic{
    width:35px;
    height: 35px;
    background: url('~@/assets/images/avatar.png') no-repeat;
    background-size: 100% 100%;
    position: relative;
    .userNav{
        z-index: 1000;
        border:1px solid #eee;
        width:100px;
