<!-- 导航组件 -->
<template>
<el-row class="tac">
    <el-col>
        <el-menu :default-active="onRoutes"
                    class="el-menu-vertical-demo"
                    background-color="#545c64"
                    text-color="#fff"
                    active-text-color="#ffd04b"
                    router>
            <template v-for="(item,index) in navList">
                <item-side :item='item' :key='index'></item-side>
            </template>
        </el-menu>
    </el-col>
</el-row>
</template>

<script>
import itemSide from './itemSide';
import { mapState } from 'vuex';
export default {
    computed: {
        onRoutes () {
            let route = this.$route;
            if ( route.meta.parentPath ) {
                return route.meta.parentPath;
            } else {
                return route.path
            }
        },
        ...mapState({
            navList: state => state.nav.navList
        })
    },
    components: {
        itemSide
    }
};
</script>

<style scoped>
.tac {
    width: 100%;
    height: 100%;
}
.el-menu-vertical-demo,
.el-col {
    height: 100% !important;
}
.toggle_block {
    display: block;
}
.abc {
    color: red;
}
</style>